
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import scrollTop from '@/components/scrollTop.vue'

const json01 = {code: 'slide4'} 
const json02 = { // 产品

order: '',
pageNum: 1,
pageSize: 4,
sort: '',
minamount: '',
maxamount: ''
}
const json03 = { // 积分
cid: '',
order: 'ASC',
pageNum: 1,
pageSize: 8
}
export default {
name: '',
components: {
scrollTop,
Swiper,
Tab,
TabItem,
XButton,
LoadMore,
Toast
},
data () {
return {
errorImg: 'this.src="' + require('../../assets/images/common/errorImg.jpg') + '"',
isJf: false,
pid: '',
searchVal: '',
toastData: {
isShow: false,
type: 'warn',
text: ''
},
banners: [],
tabs: [],
listsTop: [
{
id: 0,
name: '默认',
status: 0,
sel: false,
click: true
},
{
id: 1,
name: '价格',
status: 0, // 图标切换
sel: true, // 高低
click: false 
},
{
id: 2,
name: '销量',
status: 0,
sel: true,
click: false
},
{
id: 3,
name: '价格区间',
status: 0,
sel: false,
click: false
}
],
range: false,
price1: '',
price2: '',
products: [],
loading: false,
isLastPage: false
}
},
watch: {
'$route': function (to, from) {
let params = this.$route.params
this.pid = (params.pid + '') === '0' ? '' : (params.pid + '')
this.searchVal = params.val === 'empty' ? '' : params.val
this.loadFunc()
console.log(to)
console.log(from)
}
},
created () {
let params = this.$route.params
this.pid = (params.pid + '') === '0' ? '' : (params.pid + '')
this.searchVal = params.val === 'empty' ? '' : params.val
console.log('this.pid', this.pid)
this.loadFunc()
},
methods: {
async loadFunc () {
if (this.$route.name === 'productjs') {
this.isJf = true 
json03.cid = ''
json03.pageNum = 1

} else {
console.log('loadfun')
this.isJf = false
/* this.searchVal = value === 'empty' ? '' : value // 搜索内容 */
this.reset()
}
this.getBanners()
this.getTabs()
this.loadMore()
},
async getBanners () { // banners
let result = await this.axios.post('web/adlist', json01)
let data = result.data
let lists = []
data.map((item, index) => {
if (item.status) {
lists.push({
url: item.linkurl,
img: this.base_img + item.img,
title1: item.title
})
}
})
this.banners = lists
},
async getTabs () { // 获取tab
let lists = []
if (!this.isJf) {
let result = await this.axios.post('product/proclasslist')
let data = result.data
if (this.pid) {
lists = data.filter((item, index) => {
if (item.pid === this.pid) { // 获取对应的tab栏
return true
}
})
} else { // 全线产品

lists = data.filter((item, index) => {
if (item.pid !== '0') { // 去除系列
return true
}
})
}
} else { // 积分
let result = await this.axios.post('product/prointegralclasslist')
lists = result.data
}
this.tabs = lists
},
tab (item, index) { // tab切换
if (this.isJf) {
json03.cid = item.id
this.products = []
this.isLastPage = false
json03.pageNum = 1
this.getProductsJf()
} else {
this.reset()
this.searchVal = ''
this.pid = item.id
this.getProducts(item.id)
}
},
async getProducts (pid) { // 加载产品
if (pid === 0) {
pid = ''
}
if (this.searchVal) {
json02.title = this.searchVal

}
json02.cid = pid
let result = await this.axios.post('product/productlist', json02)
let data = result.data
if (this.isLastPage) { return }
if (data.isLastPage) {
this.isLastPage = true
}
let lists = data.list
lists = lists.filter((item, index) => {



item.pic = { // vue-lazyload
src: this.base_img + item.pic,
loading: require('../../assets/images/loading.gif'),
error: require('../../assets/images/common/errorImg.jpg')
}
return item.status / 1
})
if (json02.pageNum / 1 === 1) {
this.products = lists
} else {
this.products = this.products.concat(lists)
}
this.loading = false
json02.pageNum++
},
async getProductsJf () { // 加载积分
let result = await this.axios.post('product/prointegrallist', json03)
let data = result.data
if (this.isLastPage) { return }
if (data.isLastPage) {
this.isLastPage = true
}
let lists = data.list
lists = lists.filter((item, index) => {

let img = item.pic.split(',')
item.pic = this.base_img + img[0]
return item.status / 1
})
if (json03.pageNum / 1 === 1) {
this.products = lists
} else {
this.products = this.products.concat(lists)
}
this.loading = false
json03.pageNum++
},
loadMore () { // 上拉加载更多
if (this.isLastPage) {
return
}
this.loading = true
if (this.isJf) {
this.getProductsJf()
} else {
this.getProducts(this.pid)
}
},
reset () { // 筛选重置
json02.pageNum = 1
json02.minamount = ''
json02.maxamount = ''
json02.title = ''
json02.sort = ''
this.isLastPage = false
this.range = false
this.products = []
this.toastData.text = ''
this.toastData.isShow = false
this.toastData.type = 'warn'
this.listsTop.map((item, index) => {
item.click = false
if (index === 0) { item.click = true }
if (item.sel) {
item.status = 0
}
})
},
sel (item, index) { // 筛选
this.toastData.text = ''
this.toastData.isShow = false
this.listsTop.map((key, val) => {
key.click = false
if (val === index) {
key.click = true
}
})
let text = ''
switch (item.id) { // 排序
case 0: // 默认
text = ''
this.range = false
break
case 1: // 价格
switch (item.status) {
case 0:
item.status = 1
text = 'a.amount asc,a.ordernum desc' 
break
case 1:
item.status = 2
text = 'a.amount desc,a.ordernum asc' 
break
case 2:
item.status = 1
text = 'a.amount asc,a.ordernum desc' 
break
default: break
}
this.range = false
break
case 2: // 销量
switch (item.status) {
case 0:
item.status = 1
text = 'a.amount asc,a.ordernum asc' 
break
case 1:
item.status = 2
text = 'a.amount asc,a.ordernum desc' 
break
case 2:
item.status = 1
text = 'a.amount asc,a.ordernum asc' 
break
default: break
}
this.range = false
break
case 3: // 价格区间
this.range = true 
text = ''
break
default: break
}
if (item.id === 3) { return }
json02.sort = text
this.isLastPage = false
json02.pageNum = 1
this.products = []
json02.minamount = ''
json02.maxamount = ''
this.getProducts(this.pid)
},
price () { // 价格区间筛选
if (this.price1 && this.price2 && (this.price2 - this.price1) >= 0) {
json02.minamount = this.price1 / 1
json02.maxamount = this.price2 / 1
json02.pageNum = 1
this.isLastPage = false
this.products = []
this.getProducts(this.pid)
} else {
this.toastData.text = '请填写正确价格'
this.toastData.type = 'warn'
this.toastData.isShow = true
}
},
addCart (item, index) {
if (!localStorage.getItem('accessToken')) {
this.toastData.text = '请先登录'
this.toastData.type = 'warn'
this.toastData.isShow = true
let timer = setTimeout(() => {

clearTimeout(timer)
}, 1000)
}
if (item.number > 0) {
let json = {
mid: localStorage.getItem('username'),
number: 1,
proid: item.id,
remarks: 2
}
this.axios.post('cart/cartInsert', json).then((result) => {
console.log(result)
this.toastData.text = '已添加到购物车'
this.toastData.type = 'success'
this.toastData.isShow = true
})
} else {
console.log('售完')
}
console.log(item, index)
},
link (item, index) { // 产品详情跳转

let target = this.products[index]
let type = 1
if (this.isJf) {
type = 2
} else {
type = 1
}
this.$router.push({name: 'productDetail', params: {'type': type, 'id': target.id}})
}
}
}



